.inputOption{
    display: flex;
    align-items: center;
    margin-top: 10px;
    color: gray;
    padding: 5px;
    cursor: pointer;
}

.inputOption:hover{
    background-color: whitesmoke;
    border-radius: 10px;
}

.inputOption > h4 {
    margin-left: 5px;
    font-size: 14px;
}

@media(max-width:579px){
    .inputOption > h4 {
       display: none;
    }  
}